.ov-react-modal-overlay {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 9999;
  .ov-react-modal-overlay-background {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: -1;
    opacity: 0.75;
    background-color: black;
    pointer-events: none;
  }
  .ov-react-modal-overlay-foreground {
    display: inline-block;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    pointer-events: all;
    border: 1px solid transparent;
    background-color: white;
    color: black;
  }
}


